import { Point, PointHelper } from "./Point";
import { minilodash as _ } from "./MiniLodash";
const emptyCellData = (p) => ({
    bomb: false,
    explode: false,
    bombsAround: 0,
    explored: false,
    flagged: false,
    p
});
const isSafe = (c) => !c.bomb && !c.flagged;
export const CellHelper = {
    getText: (c) => {
        if (c.flagged) {
            return '🚩️';
        }
        if (!c.explored) {
            return '';
        }
        else if (c.bomb) {
            return '💣';
        }
        else if (c.bombsAround > 0) {
            return c.bombsAround + '';
        }
        return '';
    }
};
export class Playgroung {
    constructor(dimensions, nBombs) {
        this.intToPoint = (i) => {
            const local = i % this.nCells;
            return {
                y: Math.floor(local / this.dimensions.x),
                x: local % this.dimensions.x
            };
        };
        this.inBounds = (p) => PointHelper.isInBounds(p, this.dimensions);
        this.populateNumbers = () => {
            const cellAtPointIsBomb = (p) => this.getCell(p).bomb;
            const isValid = (p) => this.inBounds(p) && cellAtPointIsBomb(p);
            this.matrix.forEach(row => {
                row.forEach(cell => {
                    if (!cell.bomb) {
                        cell.bombsAround = PointHelper.getPointsAround(cell.p)
                            .filter(isValid)
                            .length;
                    }
                });
            });
        };
        this.quickStart = () => {
            let count = _.random(this.nCells);
            let finding = true;
            while (finding) {
                const p = this.intToPoint(count);
                const cell = this.getCell(p);
                if (isSafe(cell) && cell.bombsAround === 0) {
                    this.discover(cell);
                    finding = false;
                }
                else {
                    count += 1;
                }
            }
        };
        this.autoDiscoverFrom = (cell) => {
            this.getCellsAround(cell)
                .forEach(c => {
                if (!c.explored && isSafe(c)) {
                    c.explored = true;
                    if (c.bombsAround === 0) {
                        this.autoDiscoverFrom(c);
                    }
                }
            });
        };
        this.getCell = (p) => this.matrix[p.y][p.x];
        this.toggleFlag = (cell) => {
            cell.flagged = !cell.flagged;
        };
        this.getCellsAround = (cell) => PointHelper.getPointsAround(cell.p)
            .filter(this.inBounds)
            .map(p => this.getCell(p));
        this.exploreAround = (cell) => {
            if (cell.explored && cell.bombsAround) {
                const cells = this.getCellsAround(cell);
                const flagsAround = _.sumBy(cells, c => c.flagged ? 1 : 0);
                if (flagsAround === cell.bombsAround) {
                    cells.forEach(c => {
                        if (!c.explored && !c.flagged) {
                            this.discover(c);
                        }
                    });
                }
            }
        };
        if (!PointHelper.isPositive(dimensions)) {
            throw new Error("Dimensions must be positive");
        }
        if (nBombs < 0) {
            throw new Error("Number of bombs must be positive");
        }
        else if (nBombs >= (PointHelper.area(dimensions) / 3)) {
            throw new Error("Too many bombs");
        }
        this.dimensions = dimensions;
        this.nCells = PointHelper.area(this.dimensions);
        this.bombs = nBombs;
        this.started = false;
        this.gameOver = false;
        this.buildMatrix();
        this.initBombs();
        this.populateNumbers();
    }
    buildMatrix() {
        this.matrix = _.range(this.dimensions.y)
            .map(i => _.range(this.dimensions.x)
            .map(j => emptyCellData(Point(j, i))));
    }
    initBombs() {
        let pending = this.bombs;
        let counter = 0;
        while (pending !== 0) {
            counter += _.random(97);
            const p = this.intToPoint(counter);
            const cell = this.matrix[p.y][p.x];
            if (!cell.bomb) {
                cell.bomb = true;
                pending -= 1;
            }
        }
    }
    discover(cell) {
        if (this.gameOver) {
            return;
        }
        this.started = true;
        cell.explored = true;
        if (cell.bomb) {
            cell.explode = true;
            this.gameOver = true;
        }
        else if (isSafe(cell) && cell.bombsAround === 0) {
            this.autoDiscoverFrom(cell);
        }
    }
}
